<template>
    <div class="app">
        <div class="sidebar">
            <h2 class="sidebar-title">Â∑•ÂÖ∑ËèúÂçï</h2>
            <TreeMenu
                    :menu-data="menuData"
                    v-model:searchQuery="searchQuery"
                    @item-selected="handleItemSelected"
            />
        </div>
        <div class="content">
            <router-view />
        </div>
    </div>
</template>

<script>
import TreeMenu from './components/TreeMenu.vue';

export default {
    components: {
        TreeMenu,
    },
    data() {
        return {
            searchQuery: '',
            menuData: [
                {
                    name: 'Êó∂Èó¥Êà≥ËΩ¨Êç¢',
                    path: '/timeTools',
                },
                {
                    name: 'JSONÊ†ºÂºèÂåñ',
                    path: '/parseJson',
                },
                {
                    name: 'Êõ¥Â§ö',
                    isOpen: false,
                    children: [
                        {
                            name: 'Profile',
                            path: '/profile',
                        },
                        {
                            name: 'Security',
                            path: '/security',
                        },
                    ],
                },
            ],
        };
    },
    methods: {
        handleItemSelected(item) {
            this.searchQuery = '';
            this.expandParentMenu(item);
        },
        expandParentMenu(item) {
            const findParent = (menu, target) => {
                for (const menuItem of menu) {
                    if (menuItem.children && menuItem.children.includes(target)) {
                        menuItem.isOpen = true;
                        return;
                    }
                    if (menuItem.children) {
                        findParent(menuItem.children, target);
                    }
                }
            };
            findParent(this.menuData, item);
        },
    },
};
</script>

