<template>
    <div class="json-formatter">
        <div class="input-area">
            <h2>JSON ËæìÂÖ•</h2>
            <textarea
                    v-model="jsonInput"
                    placeholder="ËØ∑ËæìÂÖ• JSON Â≠óÁ¨¶‰∏≤"
                    class="json-input"
                    :class="{ error: errorMessage }"
            ></textarea>
            <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
            <div class="button-group">
                <button @click="formatJson">Ê†ºÂºèÂåñ JSON</button>
                <button @click="compressJson" class="compress-btn">ÂéãÁº© JSON</button>
            </div>
        </div>

        <div class="output-area">
            <h2>Ê†ºÂºèÂåñ / ÂéãÁº©ÁªìÊûú</h2>
            <div v-if="formattedJson" class="json-output">
                <json-pretty :data="parsedJson" :deep="3"></json-pretty>
            </div>
            <p v-else class="placeholder">Ê†ºÂºèÂåñÊàñÂéãÁº©ÂêéÁöÑ JSON Â∞ÜÂú®Ê≠§ÊòæÁ§∫</p>
        </div>
    </div>
</template>

<script>
import { ref } from "vue";
import JsonPretty from "vue-json-pretty";
import "vue-json-pretty/lib/styles.css";

export default {
    name: "JsonFormatter",
    components: {
        JsonPretty,
    },
    setup() {
        const jsonInput = ref("");
        const formattedJson = ref("");
        const parsedJson = ref(null);
        const errorMessage = ref("");

        // Ê†ºÂºèÂåñ JSON
        const formatJson = () => {
            try {
                parsedJson.value = JSON.parse(jsonInput.value);
                formattedJson.value = JSON.stringify(parsedJson.value, null, 2);
                errorMessage.value = "";
                jsonInput.value = formattedJson.value; // Êõ¥Êñ∞ËæìÂÖ•Ê°Ü
            } catch (error) {
                errorMessage.value = `‚ùå JSON Ê†ºÂºèÈîôËØØÔºö${error.message}`;
                formattedJson.value = "";
                parsedJson.value = null;
            }
        };

        // ÂéãÁº© JSON
        const compressJson = () => {
            try {
                parsedJson.value = JSON.parse(jsonInput.value);
                formattedJson.value = JSON.stringify(parsedJson.value); // Êó†Á©∫Ê†ºÂéãÁº©
                errorMessage.value = "";
                jsonInput.value = formattedJson.value; // Êõ¥Êñ∞ËæìÂÖ•Ê°Ü
            } catch (error) {
                errorMessage.value = `‚ùå JSON Ê†ºÂºèÈîôËØØÔºö${error.message}`;
                formattedJson.value = "";
                parsedJson.value = null;
            }
        };

        return {
            jsonInput,
            formattedJson,
            parsedJson,
            errorMessage,
            formatJson,
            compressJson,
        };
    },
};
</script>

