<template>
    <div class="unix-timestamp">
        <div class="container">
            <!-- ÂÆûÊó∂Êó∂Èó¥Êà≥ -->
            <div class="section">
                <h2>ÂΩìÂâç Unix Êó∂Èó¥Êà≥</h2>
                <p class="timestamp">{{ timestamp }}</p>
                <button @click="copyTimestamp(timestamp)">Â§çÂà∂Êó∂Èó¥Êà≥</button>
            </div>

            <!-- Êó∂Èó¥ËΩ¨Êó∂Èó¥Êà≥ -->
            <div class="section">
                <h2>Êó∂Èó¥ËΩ¨Êó∂Èó¥Êà≥</h2>
                <div class="input-group">
                    <input v-model="timeInput" placeholder="YYYY-MM-DD HH:mm:ss" class="input" />
                    <button @click="convertTimeToTimestamp">ËΩ¨Êç¢</button>
                </div>
                <p v-if="convertedTimestamp" class="result">
                    Êó∂Èó¥Êà≥Ôºö{{ convertedTimestamp }}
                    <button @click="copyTimestamp(convertedTimestamp)">Â§çÂà∂</button>
                </p>
            </div>

            <!-- Êó∂Èó¥Êà≥ËΩ¨Êó∂Èó¥ -->
            <div class="section">
                <h2>Êó∂Èó¥Êà≥ËΩ¨Êó∂Èó¥</h2>
                <div class="input-group">
                    <input v-model="timestampInput" placeholder="ËæìÂÖ•Êó∂Èó¥Êà≥ÔºàÊØ´ÁßíÔºâ" class="input" />
                    <button @click="convertTimestampToTime">ËΩ¨Êç¢</button>
                </div>
                <p v-if="convertedTime" class="result">
                    Êó∂Èó¥Ôºö{{ convertedTime }}
                    <button @click="copyTimestamp(convertedTime)">Â§çÂà∂</button>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from "vue";
import { ElNotification } from "element-plus";

export default {
    name: "TimeTools",
    setup() {
        const timestamp = ref(Date.now());
        const timeInput = ref("");
        const timestampInput = ref("");
        const convertedTimestamp = ref(null);
        const convertedTime = ref(null);
        let intervalId = null;

        const updateTimestamp = () => {
            timestamp.value = Date.now();
        };

        onMounted(() => {
            intervalId = setInterval(updateTimestamp, 100);
        });

        onUnmounted(() => {
            if (intervalId) {
                clearInterval(intervalId);
            }
        });

        const copyTimestamp = (text) => {
            navigator.clipboard
                    .writeText(text.toString())
                    .then(() => {
                        ElNotification({
                            title: "ÊàêÂäü",
                            message: "Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÔºÅ",
                            type: "success",
                            duration: 2000,
                        });
                    })
                    .catch((err) => console.error("Â§çÂà∂Â§±Ë¥•Ôºö", err));
        };

        const convertTimeToTimestamp = () => {
            const date = new Date(timeInput.value);
            if (isNaN(date.getTime())) {
                ElNotification({
                    title: "ÈîôËØØ",
                    message: "ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊó∂Èó¥Ê†ºÂºèÔºàYYYY-MM-DD HH:mm:ssÔºâ",
                    type: "error",
                    duration: 3000,
                });
                return;
            }
            convertedTimestamp.value = date.getTime();
        };

        const convertTimestampToTime = () => {
            const timestampValue = parseInt(timestampInput.value, 10);
            if (isNaN(timestampValue)) {
                ElNotification({
                    title: "ÈîôËØØ",
                    message: "ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊó∂Èó¥Êà≥ÔºàÊØ´ÁßíÔºâ",
                    type: "error",
                    duration: 3000,
                });
                return;
            }
            const date = new Date(timestampValue);
            convertedTime.value = formatDate(date);
        };

        const formatDate = (date) => {
            const year = date.getFullYear();
            const month = String(date.getMonth() + 1).padStart(2, "0");
            const day = String(date.getDate()).padStart(2, "0");
            const hours = String(date.getHours()).padStart(2, "0");
            const minutes = String(date.getMinutes()).padStart(2, "0");
            const seconds = String(date.getSeconds()).padStart(2, "0");
            return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
        };

        return {
            timestamp,
            timeInput,
            timestampInput,
            convertedTimestamp,
            convertedTime,
            copyTimestamp,
            convertTimeToTimestamp,
            convertTimestampToTime,
        };
    },
};
</script>


